<template>
  <div class="register">
    <!-- 头部 -->
    <h1>欢迎注册XXX系统</h1>
    <div class="top_hat"></div>
    <!-- 注册盒子 -->
    <div class="register-box">
      <!-- 用户昵称 -->
      <div class="input-res-group"
           :class="{ active: act_index === 1, error: errors.has('name') }"
      >
        <label for="name"></label>
        <input @focus="act_index = 1"
         name="name"
         type="text" v-model="name" 
         id="name" 
         placeholder="用户昵称"
         v-validate="{ required: true, max: 20, min: 6 }"
        />
      </div>
      <!-- 注册账号 -->
      <div class="input-res-group"
        :class="{ active: act_index === 2, error: errors.has('username') }"
      >
        <label for="username"></label>
        <input
          name="username"
          @focus="act_index = 2"
          type="text"
          v-model="username"
          id="username"
          placeholder="账号"
          v-validate="{ required: true, max: 15, min: 6 }"
        />
      </div>
      <!-- 注册密码 -->
      <div class="input-res-group"
      :class="{ active: act_index === 3, error: errors.has('password') }"
      >
        <label for="password"></label>
        <input
          name="password"
          @focus="act_index = 3"
          type="password"
          v-model="password"
          id="password"
          placeholder="密码"
          v-validate="{ required: true, max: 15, min: 6 }"
          
        />
      </div>
      <!-- 确认密码 -->
      <div class="input-res-group"
        :class="{ active: act_index === 4, error: errors.has('re-password') }"
      >
        <label for="re_password"></label>
        <input
          name="re-password"
          @focus="act_index = 4"
          type="password"
          v-model="re_password"
          id="re_password"
          placeholder="确认密码"
          v-validate="{ required: true, max: 15, min: 6 }"
        />
        <!-- <span>11111{{passwordCheckValidate.errorText}}</span> -->
      </div>
      <!-- 用户邮箱 -->
      <div class="input-res-group"
      :class="{ active: act_index === 5, error: errors.has('email') }"
      >
        <label for="email"></label>
        <input
          input-email
          name="email"
          @focus="act_index = 5"
          type="email"
          v-model="email"
          id="email" 
          placeholder="用户邮箱" 
          v-validate="{ required: true, max: 25, min: 4 }"
        />
      </div>
      <!-- 出生日期 -->
      <div class="input-res-group"
      >
          <div class="selector selectYear date" @click="selectYear" >
            
            <div class="show_year">
            <p>出生日期</p>
            <p v-if="!isClicked">2009</p>
            <p v-if="isClicked">{{year}}</p>
            <p>-</p>
            <p v-if="!isClicked">01</p>
            <p v-if="isClicked">{{month}}</p>
            <p>-</p>
            <p v-if="!isClicked">01</p>
            <p v-if="isClicked">{{date}}</p>
      </div>
    </div>
          <mt-datetime-picker
            ref="picker"
            type="date"
            year-format="{value} 年"
            month-format="{value} 月"
            date-format="{value} 日"
            v-model="pickerVisible"
            class="picker-date"
            :endDate="new Date()" 
            @confirm="handleConfirm">
            </mt-datetime-picker>

      </div>
      <!-- 验证码 -->
      <div class="input-res-group"
      :class="{ active: act_index === 6, error: errors.has('status') }"
      >
        <label for="status"></label>
        <input 
          name="status"
          @focus="act_index = 6"
          type="text" 
          v-model="status" 
          id="status" 
          placeholder="验证码（4-8位）" 
          v-validate="{ required: true, max: 8, min: 4 }"
          />
      </div>
    </div>
    <div class = "register-wrap"
    >
        <router-link to = "/">
          <div class="register-no">返回主页</div>
        </router-link>
      <div class="register-yes" :plain="true" @click="registerBtnClick"><p>确认注册</p></div>   
    </div>
    
  </div>
</template>

<script>
import axios from "axios";
import { Indicator } from "mint-ui";
export default {
  name: "Register",
  data() {
    return {
      act_index: 1,
      name: "",
      username: "",
      email: "",
      password: "",
      re_password: "",
      phone: "",
      captcha: "",
      status: "",
      year: '',
      month: '',
      pickerVisible: '',
      date:'',
      isClicked: false,
      roundButton: true
    };
  },
  mounted() {
    // 强制执行校验
    this.$validator.validate();
  },
  methods: {
       
      selectYear () {
      this.$refs.picker.open();
    },
    handleConfirm (value) {
      console.log(value);
      this.year = value.getFullYear();
      this.month = value.getMonth() + 1;
      this.date = value.getDate();
      this.isClicked = true;
    },
    registerBtnClick () {
        if (this.errors.any()) {
        return;
      }      
        if(this.password !== this.re_password) {
        this.$message('两次输入的值不一样');
        }else{
          this.$messagebox({
          title: '提示',
          message: '注册成功',
          showCancelButton: true,
          confirmButtonText:"确认",
          cancelButtonText:"返回首页"
        }).then(action => {
          if(action == 'confirm'){
            this.$router.push("/login");
            }else{
            this.$router.push("/");
            }
          });
        }
        

      axios
        .post("http://localhost:8080/ssm_war_exploded/user/register/", {
          username: this.username,
          password: this.password,
          name: this.name,
          email: this.email,
          // status: this.status
        })
        .then(res => {
          res = res.data
          if(res.data) {
                const data = res.data
                this.username = data.username
                this.password = data.password
                this.name = data.name
                this.email = data.email
                // this.status = data.status
            }
          console.log("注册成功")
          Indicator.close();
        })
        .catch(e => {
          console.log("注册失败", e);
        });
    },


  },
};
</script>

<style lang="scss" scoped>
.register {
  background: #2ade69;
  color: #10903d;
  height: 100%;
  h1 {
  text-align: center;
  font-size: px2rem(36);
  height: px2rem(36);
  padding: px2rem(68) 0 px2rem(44) 0;
  line-height: px2rem(36);
  color: #fff;
}
  .top_hat {
    width: px2rem(537);
    height: px2rem(18);
    background-color: #eee;
    margin: 0 auto;
    border-radius: px2rem(18) px2rem(18) 0 0;
  }
  .register-box {
    width: px2rem(600);
    height: px2rem(950);
    margin: 0 auto;
    background-color: #fff;
    border-radius: px2rem(20);
    display: flex;
    flex-direction: column;
      .show_year {
          display: flex;
          p {
              margin-right: 10px;
          }
          p:first-child {
              display: inline-block;
              margin-right: 80px;
          }
      }
      .date {
          width: px2rem(559);
          height: px2rem(130);
          line-height: px2rem(130);
          padding-left: px2rem(37);
          background: #fff;
          color: #757575;
          font-size: px2rem(36);
      }
    .input-email {
        background: #fff;
    }
    
    .input-res-group:last-child {
        // border-radius: 0 0 px2rem(20) px2rem(20);
        border: none;
    }
    input {
      border: 0 none;
      font-size: px2rem(36);
      line-height: px2rem(90);
      width: px2rem(560);
      padding-left: 1em;
      background: none;
      height: px2rem(130);
      color: #757575;
      outline:none;
    }
    input::-webkit-input-placeholder { /* WebKit browsers 适配谷歌 */
        color: #757575;
    }
    input:-moz-placeholder { /* Mozilla Firefox 4 to 18 适配火狐 */
        color: #757575;
    }
    input::-moz-placeholder { /* Mozilla Firefox 19+ 适配火狐 */
        color: #757575;
    }
    input:-ms-input-placeholder { /* Internet Explorer 10+  适配ie*/
        color: #757575;
    }
    .block {
        height: px2rem(130);
        span{
            display: inline-block;
            height:px2rem(130);
            line-height: px2rem(130);
            font-size: px2rem(36);
            padding-left: px2rem(36); 
            color: #757575;
        }
    }
  }
  .input-res-group.active {
    color: #10903d;
    border-bottom: 2px solid #10903d;
  }
  .input-res-group.error
   {
    color: red;
    border-bottom: 2px solid red;
    // border-radius: px2rem(45);
  }
  .register-wrap {
      display: flex;
      width: px2rem(600);
      margin: 0 auto;
    .register-no {
      width: px2rem(250);
      height: px2rem(100);
      margin: 0 auto;
      margin: 0;
      margin-top: px2rem(60);
      background-color: #fff;
      border-radius: px2rem(20);
      font-size: px2rem(36);
      line-height: px2rem(100);
      text-align: center;
      font-weight: 700;
      color: #10903d;
    }
  .register-yes {
    width: px2rem(250);
    height: px2rem(100);
    margin: 0 auto;
    margin-left: px2rem(100);
    margin-top: px2rem(60);
    background-color: #fff;
    border-radius: px2rem(20);
    font-size: px2rem(36);
    line-height: px2rem(100);
    text-align: center;
    font-weight: 700;
    color: #10903d;
  }
  }
}
</style>

