<template>
  <div class="login">
    <h1>欢迎登录XXX系统</h1>
    <div class="top_hat"></div>
    <div class="login-box">
      <!-- login -->
      <div class="logo-wrap">
        <div class="logo-box"></div>
      </div>
      <!-- 登录账号 -->
      <div
        class="input-group"
        :class="{ active: act_index === 1, error: errors.has('username') }"
      >
        <label for="username">账号：</label>
        <input
          @focus="act_index = 1"
          type="text"
          id="username"
          v-model="username"
          name="username"
          v-validate="{ required: true, max: 20, min: 6 }"
        />
      </div>
      <!-- 登录密码 -->
      <div
        class="input-group"
        :class="{ active: act_index === 2, error: errors.has('pwd') }"
      >
        <label for="password">密码：</label>
        <input
          @focus="act_index = 2"
          type="password"
          id="password"
          v-model="password"
          name="pwd"
          v-validate="{ required: true, max: 15, min: 6 }"
        />
      </div>
      <!-- 验证码 -->
      <div
        class="input-group auth-group"
        :class="{ active: act_index === 3, error: errors.has('auth') }"
      >
        <label for="auth_code_input"></label>
        <input
          @focus="act_index = 3"
          type="au-code"
          id="auth-code-input"
          v-model="auth_code_input"
          name="auth"
          v-validate="{ required: true, max: 6, min: 4 }"
        />
        <!-- 图片 -->
        <div class="auth-code" ><img src="" alt="" >图片</div>
      </div>

      <div class="btn-row">
        
        <button class="btn-box-wrap" @click="loginBtnClick">
          <!-- <router-link to="/">登录</router-link> -->
          登录
        </button>
        
      </div>
    </div>
    <router-link to = "/register">
     <div class="register-wrap"><p>注册</p></div> 
    </router-link>
    
  </div>
</template>

<script>
import axios from "axios";
import { Indicator } from "mint-ui";


export default {
  name: "Login",
  data() {
    return {
      act_index: 1,
      username: "",
      cno: "",
      password: "",
      auth:"",
      auth_code_input:""
    };
  },
  mounted() {
    // 强制执行校验
    this.$validator.validate();
  },
  methods: {
    loginBtnClick() {
      // 判断当前是否全部校验通过
      //Boolean 如果有错误返回true,否则 false
      if (this.errors.any()) {
        return;
      }
      // 弹出等待的遮罩层，防止二次点击
      Indicator.open("加载中...");
      // 发送ajax请求
      axios
        .post("http://localhost:8080/ssm_war_exploded/user/login/", {
          username: this.username,
          // cno: this.cno,
          password: this.password,
        },
        {

        })
        .then(res => {
          res = res.data
          if(res.data) {
                const data = res.data
                this.username = data.username
                this.password = data.password
            }
          console.log("登录成功")
          Indicator.close();
          this.$router.push("/");
        })
        .catch(() => {
          // console.log("登录失败", e);
          Indicator.close();
          this.$message('密码错误');
        });
    },
    
  }
  // components: {
  //       HelloWorld
  // },
};
</script>

  <style lang="scss" scoped>
h1 {
  text-align: center;
  font-size: px2rem(36);
  height: px2rem(36);
  padding: px2rem(68) 0 px2rem(44) 0;
  line-height: px2rem(36);
  color: #fff;
}

.login {
  background-color: #2ade69;
  height: 100%;

  .top_hat {
    width: px2rem(537);
    height: px2rem(18);
    background-color: #eee;
    margin: 0 auto;
    border-radius: px2rem(18) px2rem(18) 0 0;
  }

  .login-box {
    width: px2rem(600);
    height: px2rem(936);
    margin: 0 auto;
    background-color: #fff;
    border-radius: px2rem(20);

    .logo-wrap {
      padding: px2rem(80) 0;
      .logo-box {
        width: px2rem(190);
        height: px2rem(190);
        margin: 0 auto;
        background: url(../assets/logo.png);
        background-size: cover;
      }
    }

    .input-group {
      border: 2px solid #e2e2e2;
      border-radius: px2rem(45);
      font-size: px2rem(28);
      line-height: px2rem(90);
      padding: 0 px2rem(36);
      color: #757575;
      width: px2rem(400);
      margin: 0 auto px2rem(40);
    }
    input {
      border: 0 none;
      font-size: px2rem(28);
      line-height: px2rem(90);
      width: px2rem(200);
      padding-left: 1em;
      background: none;
    }
  }
  .input-group.active {
    color: #10903d;
    border: 2px solid #10903d;
  }
  .input-group.error
   {
    color: red;
    border: 2px solid red;
    border-radius: px2rem(45);
  }

  .auth-group {
    display: flex;
    justify-content: space-around;
    .auth-code-img {
      width: px2rem(300);
      border: 1px solid #ccc;
    }
    .auth-code {
      font-size: px2rem(28);
      color: #757575;
      border: none;
      border-radius: px2rem(45);
    }
  }

  .btn-row {
    width: px2rem(476);
    display: flex;
    justify-content: space-around;
    margin: 0 auto;

    .btn-box-wrap {
      width: px2rem(476);
      height: px2rem(100);
      background-color: #2ade69;
      border-radius: px2rem(20);
      border: none;
      font-size: px2rem(36);
      color: #fff;
      font-weight: 700;
    }
  }
  .register-wrap {
    width: px2rem(600);
    height: px2rem(100);
    margin: 0 auto;
    margin-top: px2rem(60);
    background-color: #fff;
    border-radius: px2rem(20);
    font-size: px2rem(36);
    line-height: px2rem(100);
    text-align: center;
    font-weight: 700;
    color: #757575;
  }

  .auth-code-wrap.active {
    color: #10903d;
    border: 2px solid #10903d;
  }
}
</style>
  
  <style >
html,
body,
#app {
  background-color: #2ade69;
  height: 100%;
}
</style>